<template>
  <div class="map__section">
    <div class="map__section-item">
      <p class="map__section-item-title"> {{ title }}</p>
      <ul class="location-tabs">
        <li v-for="item in location_list" class="location-tabs__item" v-bind:class="{ active: item.is_active_tab }" @click="checkActiveTab(item)">
          {{ item.city_name }}
        </li>
      </ul>
      <div v-for="item in location_list" class="location-tabs__content" v-bind:class="{ active: item.is_active_tab }">
        <p class="location-tabs__content-title">  {{ item.center_name }}  </p>
        <span v-for="(value, key) in item.center_adress" class="location-tabs__content-text">
          {{ value }}
        </span>
      </div>
    </div>   

     <div class="map__section-item map-container">
        <div id="map" class="map"></div>
    </div>
 </div>
</template>

<script>
export default {
  name: 'Map',
  data () {
    return {
      title: 'Our offices',
      map: null,
      tileLayer: null,
      location_list: {
        location_ukraine: {
          city_name: 'kyiv',
          city_coords: [50.488, 30.479],
          center_name: 'Global Message Services Ukraine LLC',
          center_adress: {
            street: 'Kyiv, Stepan Bandera, 33',
            post_code: '02066',
            country: 'Ukraine'
          },
          is_active_tab: true,
        },
        location_usa: {
          city_name: 'new york',
          city_coords: [40.748, -73.985],
          center_name: 'Global Message Services USA LLC',
          center_adress: {
            street: 'New York, 20 W 34th St',
            post_code: '10001',
            country: 'USA'
          },
          is_active_tab: false
        },
        location_china: {
          city_name: 'guangzhou',
          city_coords: [23.125, 113.266],
          center_name: 'Global Message Services China LLC',
          center_adress: {
            street: 'Guangzhou, Renkang Li, Yuexiu District',
            post_code: '510375',
            country: 'China'
          },
          is_active_tab: false
        },
        location_spain: {
          city_name: 'barcelona',
          city_coords: [41.389, 2.162],
          center_name: 'Global Message Services Spain LLC',
          center_adress: {
            street: 'Barcelona, Carrer de Balmes, 60',
            post_code: '08001',
            country: 'Spain'
          },
          is_active_tab: false
        }                          
      }
    }
  },
  mounted() { 
    this.initMap();
  },
  methods: { 
    initMap() {
      this.map = L.map('map' , {
        center: [50.488, 30.479],
        zoom: 12,
        zoomControl: false
      });
      this.tileLayer = L.tileLayer('https://cartodb-basemaps-{s}.global.ssl.fastly.net/dark_all/{z}/{x}/{y}.png',
        {
        maxZoom: 18
        }
      );
     
      let myIcon = L.divIcon({ 
        iconSize: new L.Point(36, 36), 
        className: 'map__marker'
      });

      this.marker =  L.marker([50.488, 30.479], {icon: myIcon});
      this.tileLayer.addTo(this.map);
      this.marker.addTo(this.map)
    },
    checkActiveTab(currentObj) {
     for(let item in this.location_list) {
      this.location_list[item].is_active_tab = false
     }
     currentObj.is_active_tab = true;
     this.map.setView(currentObj.city_coords);
     this.marker.setLatLng(currentObj.city_coords)
    }
  }
}
</script>
<style>
  @import url('https://unpkg.com/leaflet@1.2.0/dist/leaflet.css');
</style>




