<template>
  <div id="contacts" class="form__section":style="{'background-image': 'url(' + require('../assets/images/pattern.png') + ')'}">
    <h1 class="form__section-title">{{ title }}</h1>
    <div class="form__section-item">
    <form class="form__item" @submit="sendForm" method="post" action="http://httpbin.org/post">
      <p v-if="errors.length" class="errors__container">
       <span v-for="error in errors">{{ error }}</span>
      </p>
      <p class="form__item-field" v-bind:class="[name.state]" @click="checkActive()">
        <label for="name" class="form__label">{{ name.field_name }}</label>
        <input id="name" v-model="name.value" @change="checkForm(name)" type="text" name="name" class="form__input">
      </p>
      <p class="form__item-field" v-bind:class="[phone.state]" @click="checkActive()">
        <label for="phone" class="form__label">{{ phone.field_name }}</label>
        <input id="phone" v-model="phone.value" @change="checkForm(phone)" type="tel" name="phone" class="form__input">
      </p>
      <p class="form__item-field" v-bind:class="[email.state]" @click="checkActive()">
        <label for="email" class="form__label">{{ email.field_name }}</label>
        <input id="email" v-model="email.value" @change="checkForm(email)" type="email" name="email" class="form__input">
      </p>
      <p class="form__item-field checkbox">
         <input id="checkbox" type="checkbox" v-model="checkbox.checked" name="checkbox" class="form__input-checkbox">
         <span class="form__checkbox checked" @click="changeCheckBoxState()"></span>
         <label for="checkbox" class="form__label checkbox" @click="changeCheckBoxState()">{{ form_checkbox }} </label>
         <span v-if="!checkbox.checked" class="notification-text">{{ checkbox.checkbox_msg }}</span>
      </p>
      <input type="submit" value="Get in touch" class="form__btn-submit" v-bind:class="{ disabled: formSend }">
    </form>
    </div>
    <div class="form__section-item">{{ form_text }}</div>
  </div>
</template>

<script>
export default {
  name: 'Form',
  data () {
    return {
      title: 'contact us',
      form_text: 'Please tell us more about your request and give us info about your company and country',
      form_checkbox: 'I agree the processing of personal data',
      errors: [],
      formSend: false,
      name: {
        field_name: 'Name',
        value: null,
        state: ''
      },
      phone: {
        field_name: 'Phone',
        value: null,
        state: ''
      },
      email: {
        field_name: 'E-mail',
        value: null,
        state: ''
      },
      checkbox: {
        checked: true,
        checkbox_msg: 'We can not send form without your agreement'
      },
      errors: []
    }
  },
  methods: {
    checkForm(fieldObj) {
      let reg_pattern;

      if(fieldObj.field_name == 'Name'){
          reg_pattern = /^[A-Za-z]+$/;
      } else if (fieldObj.field_name == 'Phone'){
         reg_pattern = /^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$/;
      } else if(fieldObj.field_name == 'E-mail') {
        reg_pattern = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      }

     
      if (!fieldObj.value ) {
        this.errors.length > 0 ? "" : this.errors.push('Please, enter your' + ' ' + fieldObj.field_name.toLowerCase());
        fieldObj.state =  'error';
      } else if(!reg_pattern.test(fieldObj.value)) {
        this.errors.length > 0 ? "" : this.errors.push('Please, enter correctly your' + ' ' + fieldObj.field_name.toLowerCase());
        fieldObj.state =  'error';
      } else {
        fieldObj.state =  '';
        this.errors = [];
      }
    },
    checkActive() {
      let fieldsItems = document.getElementsByClassName('form__item-field');
      let currentField = event.target.parentNode;
      for(let i = 0; i < fieldsItems.length; i++ ){
        fieldsItems[i].classList.remove('active');
      }
      currentField.classList.add('active');
      
    },
    changeCheckBoxState() {
      let checkbox = document.getElementsByClassName('form__checkbox')[0];
      checkbox.classList.toggle('checked');
      this.checkbox.checked = !this.checkbox.checked
     
    },
    sendForm(e) {
       if(!this.errors.length && this.name.value && this.phone.value && this.email.value && this.checkbox.checked){
          this.formSend = true;
       } else {
          e.preventDefault();
       }      
    }
  },
}
</script>



