<template>
  <div class="main__section">
    <h1 class="main__section-title">{{ title }}</h1>
    <div class="main__section-container">
      <div class="main__section-item">
        <p class="main__section-item-title headquarters"> 
          <span class="main__section-item-subtitle"> {{ headquarters_subtitle }} </span>
          {{ headquarters_title }}
        </p>
     
        <div class="main__section-item-info contacts" >
           <span v-for="item of headquarters_address"> {{ item }}</span>
           <a v-bind:href="'tel:' + headquarters_phone" class="main__section-item-phone">{{ headquarters_phone }}</a>
        </div>
       
        <div class="main__section-item-info contacts">
          <span class="contacts-list" v-for="(value, key) in headquarters_contacts">
            <span class="contacts-item">{{ key }} </span>
            <span class="contacts-info"><a :href="value.link"> {{ value.name }}</a></span>
            <span></span>
          </span>
        </div>
      </div>
      <div class="main__section-item">
        <p class="main__section-item-title">{{ support_title }} 
          <span class="main__section-item-subtitle"> {{ support_subtitle }} </span>
        </p>
        <div class="main__section-item-info"> {{ support_info }}</div>
        <a v-bind:href="'tel:' + support_phone" class="main__section-item-phone"> {{ support_phone }}</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Main',
  data () {
    return {
      title: 'Contacts',
      headquarters_title: 'Headquarters',
      headquarters_subtitle: 'GMS',
      headquarters_address: {
      company_name: 'Global Message Services Ukraine LLC',
      company_address: 'Bundesstrasse 5 | 3rd Floor',
      country_post_adress: '6300 Zug Switzerland'
      },
      headquarters_phone: '+41 41 544 62 00',
      headquarters_contacts: {
        'Email' : {
          name: 'info@gms-worldwide.com',
          link: 'mailto:info@gms-worldwide.com'
        },
        'Send message' : {
          name: 'Viber us',
          link: 'viber://add?number=номер телефона'
        },
        'Send form' : {
          name: 'Contact us ',
          link: '#contacts'
        },
      },
      support_title: 'Technical support',
      support_subtitle: '24/7',
      support_info: 'GMS understands that it can be hard to follow all new technologies. We regularly provide for all our clients educational trainings to make your operator`s business earn more and better though our best practices.',
      support_phone: '+41 41 544 62 06'
    }
  }
}
</script>

<style>

</style>



